// src/components/AudioTranscriber.jsx
import React, { useState, useRef, useEffect } from 'react';
import micIcon from '../assets/icons/mic-icon.png';
import styles from './AudioTranscriber.module.css';
import { useRecorder } from '../hooks/useRecorder';

export default function AudioTranscriber() {
  const [isRecording, setIsRecording] = useState(false);
  const [transcription, setTranscription] = useState('');

  const [lang, setLang] = useState('auto');
  const [speakerVerification, setSpeakerVerification] = useState(false);

  const recorder = useRecorder();
  const wsRef = useRef(null);
  const intervalRef = useRef(null);

  useEffect(() => {
    return () => {
      // 组件卸载时清理
      stopRecording();
    };
  }, []);

  function startRecording() {
    if (isRecording) return;

    // 构造WebSocket地址，注意根据实际地址修改
    let params = [];
    if (lang) params.push(`lang=${encodeURIComponent(lang)}`);
    if (speakerVerification) params.push('sv=1');
    const queryStr = params.length > 0 ? `?${params.join('&')}` : '';
    const wsUrl = `ws://192.168.168.77:6007/ws/transcribe${queryStr}`;

    const ws = new WebSocket(wsUrl);
    ws.binaryType = 'arraybuffer';

    ws.onopen = () => {
      console.log('WebSocket 已连接');
      recorder.start().catch((e) => {
        alert('启动录音失败: ' + e.message);
        ws.close();
      });

      intervalRef.current = setInterval(() => {
        if (ws.readyState === 1) {
          const audioBlob = recorder.getAudioBlob();
          if (audioBlob.size > 0) {
            console.log('发送音频数据，大小：', audioBlob.size);
            ws.send(audioBlob);
            recorder.clearBuffer();
          }
        }
      }, 500);
    };

    ws.onmessage = (evt) => {
      try {
        const resJson = JSON.parse(evt.data);
        if (resJson.code === 0) {
          setTranscription((prev) => prev + '\n' + (resJson.data || '无识别结果'));
        } else {
          // 其他情况也追加显示
          setTranscription((prev) => prev + '\n' + evt.data);
        }
      } catch (e) {
        console.error('解析转写消息失败', e);
        setTranscription((prev) => prev + '\n' + evt.data);
      }
    };

    ws.onclose = () => {
      console.log('WebSocket 已关闭');
      stopRecording();
    };

    ws.onerror = (e) => {
      console.error('WebSocket 错误', e);
    };

    wsRef.current = ws;
    setIsRecording(true);
  }

  function stopRecording() {
    if (!isRecording) return;

    if (wsRef.current) {
      wsRef.current.close();
      wsRef.current = null;
    }
    recorder.stop();
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
    setIsRecording(false);
  }

  return (
    <div style={{ maxWidth: 700, margin: '20px auto', fontFamily: 'Arial, sans-serif' }}>
      <button
        onClick={() => (isRecording ? stopRecording() : startRecording())}
        style={{
          padding: '8px 16px',
          fontSize: 16,
          backgroundColor: isRecording ? 'red' : '#4CAF50',
          color: 'white',
          border: 'none',
          borderRadius: 4,
          cursor: 'pointer',
          marginBottom: 12,
        }}
      >
        {isRecording ? 'Stop Recording' : 'Start Recording'}
      </button>

      <div style={{ marginBottom: 12 }}>
        <label htmlFor="langInput" style={{ marginRight: 8 }}>
          Language:
        </label>
        <input
          id="langInput"
          type="text"
          value={lang}
          onChange={(e) => setLang(e.target.value)}
          disabled={isRecording}
          style={{ padding: 4, fontSize: 14, width: 120 }}
          placeholder="auto"
        />
      </div>

      <div style={{ marginBottom: 20 }}>
        <label>
          <input
            type="checkbox"
            checked={speakerVerification}
            disabled={isRecording}
            onChange={(e) => setSpeakerVerification(e.target.checked)}
            style={{ marginRight: 6 }}
          />
          Speaker Verification
        </label>
      </div>

      <div
        id="transcriptionResult"
        style={{
          whiteSpace: 'pre-wrap',
          backgroundColor: '#f5f5f5',
          padding: 10,
          border: '1px solid #ddd',
          borderRadius: 5,
          minHeight: 150,
          fontFamily: 'monospace',
          fontSize: 14,
          overflowY: 'auto',
          maxHeight: 300,
        }}
      >
        {transcription || 'Transcription result will be displayed here...'}
      </div>
    </div>
  );
}
